@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.homeWrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 130px;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif;
}

.left_block {
    margin-left: 77px;
}

.header {
    display: flex;
    font-size: 50px;
}

.greeting {
    font-weight: bold;
    display: flex;
    margin-top: 3px;
}

.text_about_me {
    font-size: 33.9px;
    width: 759px;
    color: #868E96;
    margin-top: 50px;
    display: flex;
}

.socImagesWrapper {
    display: flex;
}

.socImagesWrapper img {
    height: 43px;
    margin: 39.9px 13px 0px 0px;
}

.TigranTumoImg {
    height: 420px;
    margin: 30px 100px 0px 0px;
}

.buttons {
    margin-top: 39.9px;
    display: flex;
}

.button {
    height: 59px;
    color: white;
    background: red;
    border: none;
    border-radius: 5px;
    font-size: 19px;
    width: 200px;
    margin-right: 47px;
}

.buttons button:hover {
    border: 1px solid red;
    background: white;
    color: black;
    transition: 0.5s;
}

.resume {
    margin-left: 43px;
}

@media screen and (max-width: 520px) {
    .homeWrapper {
        margin-top: 95px;
        justify-content: center;
    }

    .left_block {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        margin: 0;
    }

    .header {
        font-size: 27px;
        font-family: 'Roboto', sans-serif;
        font-weight: bold;
        display: flex;
        justify-content: center;
        margin: 0;
    }

    .aboutMe {
        display: flex;
        justify-content: center;
    }

    .text_about_me {
        text-align: center;
        font-size: 17.2px;
        margin-top: 20px;
        width: 468px;
    }

    .socImagesWrapper {
        margin-top: 21px;
    }

    .socImages {
        display: flex;
        flex-wrap: wrap;
        width: 468px;
        justify-content: center;
    }

    .socImagesWrapper img {
        margin: 3px 10px 0px 0px;
        height: 40px;
    }

    .imageBlock {
        display: flex;
        justify-content: flex-end;
    }

    .TigranTumoImg {
        height: 233px;
        margin: 50px 0px 0px 0px;
    }

    .buttons {
        margin: 50px 0px 0px 0px;
        justify-content: center;
    }

    .button {
        height: 50px;
        color: white;
        background: red;
        border: none;
        border-radius: 5px;
        font-size: 15px;
        width: 150px;
        text-align: center;
        margin: 0px 47px 0px 47px;
    }

    .button:hover {
        height: 50px;
        color: white;
        background: red;
        border: none;
        border-radius: 5px;
        font-size: 17px;
        width: 150px;
        text-align: center;
        margin: 0px 47px 0px 47px;
    }

}

@media screen and (max-width: 415px) {
    .homeWrapper {
        margin-top: 90px;
    }

    .text_about_me {
        text-align: center;
        font-size: 17px;
        width: 372px;
    }

    .socImages {
        width: 372px;
    }

    .TigranTumoImg {
        height: 225px;
    }

    .button {
        height: 45px;
        font-size: 16px;
        width: 150px;
        margin: 0px 20px 0px 20px;
    }

    .button:hover {
        height: 50px;
        color: white;
        background: red;
        border: none;
        border-radius: 5px;
        font-size: 17px;
        width: 150px;
        text-align: center;
        margin: 0px 20px 0px 20px;
    }
}

@media screen and (max-width: 375.5px) {

    .homeWrapper {
        margin-top: 100px;
    }

    .text_about_me {
        font-size: 18px;
        width: 337px;
    }

    .socImages {
        width: 337px;
    }

    .TigranTumoImg {
        height: 230px;
    }

    .button {
        margin: 0px 7.7px 0px 7.7px;
    }

    .button:hover {
        margin: 0px 7.5px 0px 7.5px;
    }

}

@media screen and (max-width: 320.5px) {
    .text_about_me {
        font-size: 17.5px;
        width: 288px;
    }

    .socImages {
        width: 288px;
    }

    .TigranTumoImg {
        height: 230px;
    }

    .button {
        height: 39px;
        font-size: 13.9px;
        margin: 0px 7.7px 0px 7.7px;
        width: 130px;
    }

    .button:hover {
        margin: 0px 7.5px 0px 7.5px;
    }

}